.homepage {
  /* width: 100%; */
  width: 100vw;
  height: 81vh;
  padding-top: 50px;
  display: flex;
  position: relative;
  overflow: hidden;
  justify-content: center;
  align-items: space-between;
  color: var(--my-font-colors);
  z-index: 1;
  /* position: relative; */
  /* background-color: #fff; */
}
.homepage .homepage-body{
  width: 85%;
  overflow: hidden;
  height:100%;
  /* background-color: red; */
  position: relative;
}
.homepage .homepage-body .new-playlist-card{
    display: none;
    flex-direction: column;
    justify-content: center;
    align-items: flex-start;
    position: absolute;
    top:0;
    left:0;
    height:100vh;
    width: 100%;
    /* padding: 10px;
    background-color: var(--site-black-2);
    border: 2px solid var(--site-green);
    box-shadow:2px 2px 12px var(--site-green); */
}
.homepage .homepage-body .new-playlist-card .card-wrap{
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: flex-start;
    position: absolute;
    top:50%;
    left:0;
    width: 200px;
    padding: 10px;
    background-color: var(--site-black-2);
    border: 2px solid var(--site-green);
    box-shadow:2px 2px 12px var(--site-green);
}
.homepage .homepage-body .new-playlist-card.show-new-playlist-card{
  display: flex;
}
.homepage .homepage-body .new-playlist-card span{
  color: var(--normalwhite);
  margin-bottom: 10px;
}
.homepage .homepage-body .new-playlist-card span:last-child{
  display: flex;
  justify-content: flex-end;
  align-items: center;
  width: 100%;
}
.homepage .homepage-body.reduce-height-for-btm-pallete{
  height:65vh;
  animation: reduceHeight 500ms ease-in 1;
}
.homepage .homepage-body .content{
  display: flex;
  justify-content: center;
  align-items: flex-start;
  width: 100%; 
  height: 100%;
  color: black;
  overflow: hidden;
  /* background-color: green; */
}
.homepage .homepage-body.body-change-color{
  /* background-color: var(--normalblack); */
}
.homepage .homepage-body.increase-body{
  width: 98%; 
}



.homepage .bottom-pallete{
  display: none;
  justify-content: center;
  align-items: center;
  position: absolute;
  width: 100%;
  height: 10vh;
  background-color: rgb(47, 173, 47);
  bottom: 0;
  left: 0;
  padding: 10px;
  animation: slideUp 300ms ease-in 1;
}
.homepage .bottom-pallete.display-bottom-pallete{
  display: flex;
}
.homepage .bottom-pallete .wrap{
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  width: 100%;
}
.homepage .bottom-pallete .wrap .first-line{
  display: none;
  justify-content: center;
  align-items: flex-end;
  width: 100%;
  /* background-color: blue; */
}
.homepage .bottom-pallete .wrap .second-line{
  display: flex;
  justify-content: space-between;
  align-items: center;
  width: 100%;
  /* background-color: yellow; */
}
.homepage .bottom-pallete .wrap .second-line .box-1{
  display: flex;
  justify-content: center;
  align-items: center;
  /* background-color: grey; */
}
.homepage .bottom-pallete .wrap .second-line .box-1 div{
  display: flex;
  flex-direction: column;
}
.homepage .bottom-pallete .wrap .second-line .box-1 div span{
  margin: 2px;
}
.homepage .bottom-pallete .wrap .second-line .box-1 div span:last-child{
  font-size: 14px;
  color: rgb(215, 204, 204);
}
.homepage .bottom-pallete .wrap .second-line .img-holder{
  display: flex;
  flex-grow: 3;
  margin: 0 10px 10px 0;
  width: 50px;
  height: 50px;
}
.homepage .bottom-pallete .wrap .second-line span img{
  object-fit: cover;
  width: 100%;
  height: 100%;
  /* background-color: green; */
}
.homepage .bottom-pallete .wrap .first-line .box-2,
.homepage .bottom-pallete .wrap .second-line .box-2{
  display: flex;
  width: 50%;
  display: flex;
  justify-content: center;
  align-items: center;
  font-size: 12px;
}
.homepage .bottom-pallete .wrap .first-line .box-2{
  width: 100%;
  margin-top: 10px;
  /* background-color: aqua; */
}
.homepage .bottom-pallete .wrap .second-line .box-3{
  display: flex;
  justify-content: center;
  align-items: center;
  /* background-color: aquamarine; */
  margin: 10px 0;
}
.homepage .bottom-pallete .wrap .second-line .box-3 span{
  margin: 10px;
}
.homepage .bottom-pallete .wrap .second-line .box-3 span:last-child{
  margin-right: 0;
}
.homepage .bottom-pallete .wrap .second-line .box-3 span:nth-child(4),
.homepage .bottom-pallete .wrap .second-line .box-3 span:nth-child(5){
  padding: 10px 5px 5px 5px;
  background-color: rgb(32, 111, 32);
  border-radius: 100px;
}
.homepage .bottom-pallete .wrap .first-line .box-2 .line,
.homepage .bottom-pallete .wrap .second-line .box-2 .line{
  position: relative;
  width: 100%;
  border-bottom: 2px solid var(--normalwhite);
  margin: 10px 20px;
}
.homepage .bottom-pallete .wrap .first-line .box-2 .line .circle,
.homepage .bottom-pallete .wrap .second-line .box-2 .line .circle{
  position: absolute;
  top: -9px;
  left: 20%;
  height: 20px;
  width: 20px;
  border: 2px solid var(--normalwhite);
  border-radius: 100px;
  background-color: rgb(47, 173, 47);
}

@keyframes slideUp {
  from {transform: translateY(10vh);}
  to {transform: translateY(-1vh);}
}
@keyframes reduceHeight {
  from {height: 100%;}
  to {height: 65vh;}
}

@media screen and (max-width:768px){
  .homepage .bottom-pallete{
    height: 20vh;
  }
  .bottom-pallete.display-bottom-pallete .wrap .first-line{
    display: flex;
  }
  .bottom-pallete.display-bottom-pallete .wrap .second-line .box-2{
    display: none;
  }
  .homepage .homepage-body.reduce-height-for-btm-pallete{
    height: 55vh;
  }
}