#side-bar{
    display: flex;
    flex-wrap: wrap;
    justify-content: center;
    align-items: center;
    height: 100%;
    border-bottom: none;
    overflow-y: hidden;
    overflow-X: hidden;
    border-top: 2px solid var(--site-black);
    background-color: var(--site-black); 
}
#side-bar.reduce-height-for-btm-pallete{
  height:65vh;
  animation: reduceHeight 500ms ease-in 1;
}
#side-bar.reduce-width{
    width: auto;
}
#side-bar:hover{
    overflow-y: scroll;
}
#side-bar.no-scroll-bar:hover{
    overflow-y: hidden;
}
#side-bar .content{
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: flex-start;
    width: 100%;
}
#side-bar .content .menubar{
    background-color: rgb(176, 53, 53);
    padding: 30px 20px 30px 10px;
}
#side-bar .content .box{
    display: flex;
    justify-content: flex-start;
    align-items: flex-start;
    width: 100%;
}
#side-bar .content .box .left-side{
    display: flex;
    flex-direction: column;
    justify-content: flex-start;
    align-items: center;
    padding-top: 40px;
    background-color: rgb(176, 53, 53);
}
#side-bar .html-icon{
    font-size:25px;
    font-weight:bold;
    /* padding:0 3px 200px 3px; */
    width:100%;
}
#side-bar .content .box .left-side .icon{
    display: flex;
    justify-content: flex-start;
    align-items: center;
    margin-bottom:40px;
    padding: 10px 20px 10px 10px;
    height: 40px;
    position: relative;
    width:100%;
}
#side-bar .content .box .left-side .icon:first-child{
    /* padding-bottom:30px; */
}
/* #side-bar .content .box .left-side .icon-fill{
    height:100%;
    width:100%;
    position: absolute;
    top: 0;
    left: 0;
}
#side-bar .content .box .left-side .icon-fill:hover{
    border-radius: 20px 0 0 20px;
    border-left: 3px solid var(--normalblack);
} */
#side-bar .content .box .left-side .icon.add-px-below{
    margin-bottom:140px;
}
#side-bar .content .box .right-side{
    display: flex;
    flex-direction: column;
    justify-content: flex-start;
    align-items: flex-start;
    width: 100%;
    padding-top: 40px;
}
#side-bar .content .box .right-side.hide-content{
    display: none;
}
#side-bar .content .box .left-side .blocks,
#side-bar .content .box .right-side .blocks{
    display: flex;
    flex-direction: column;
    justify-content: flex-start;
    align-items: flex-start;
    width: 100%;
    /* position: relative; */
}
#side-bar .content .box .left-side .blocks:last-child{
    /* border-top: 1px solid var(--site-black); */
    /* margin-top:10px; */
    padding-top:30px;
}
#side-bar .content .box .right-side .blocks:last-child{
    border-top: 1px solid grey;
    /* margin-top:10px; */
    padding-top:30px;
}
#side-bar .content .box .left-side .blocks .list,
#side-bar .content .box .right-side .blocks .list{
    display: flex;
    justify-content: flex-start;
    align-items: center;
    padding:10px 10px 10px 20px;
    margin-bottom:40px;
    height: 40px;
    width: 100%;
    color:text-color;
}
#side-bar .content .box .right-side .blocks .list:first-child{
    /* margin-bottom:40px; */
}
#side-bar .content .box .right-side .blocks .list:first-child.add-px-below{
    margin-bottom:20px;
}
#side-bar .content .box .right-side .blocks .list.add-px-below{
    margin-bottom:110px;
}
#side-bar .content .box .right-side .block-1 .slide-box{
    display: none;
    flex-direction: column;
    justify-content: center;
    align-items: flex-start;
    /* position: absolute;
    top:40px;
    right:-500px; */
    width: 100%;
    animation: slideScaleClose 300ms ease-in 1;
}
#side-bar .content .box .right-side .block-1 .slide-box .sub-icon{
    margin-right:10px;
}
#side-bar .content .box .right-side .block-1 .slide-box .list{
    display: flex;
    justify-content: flex-start;
    align-items: flex-start;
    padding: 5px 15px 5px 10px;
    margin-bottom:20px;
    /* width: 100%; */
    position: relative;
}
#side-bar .content .box .right-side .block-1 .slide-box .list-fill{
    height:100%;
    width: 100%;
    top: 0;
    left: 0;
    position: absolute;
}
#side-bar .content .box .right-side .block-1 .slide-box.slide-open{
    display:flex;
    animation: slideScaleOpen 500ms ease-in 1;
}
@keyframes slideScaleOpen {
    from {transform: scaleY(0);}
    to {transform: scaleY(1);}
}
@keyframes slideScaleClose {
    from {transform: scaleY(1);}
    to {transform: scaleY(0);}
}



@media screen and (max-width:768px){
    #side-bar .content .menubar{
        padding-right: 10px;
    }
    #side-bar .content .box .left-side .icon{
        padding-right: 10px;
    }
    #side-bar.reduce-height-for-btm-pallete{
      height: 55vh;
    }
