
.song-li{
  display: flex;
  justify-content: flex-start;
  align-items: center;
  position: relative;
  width: 100%;
  /* border: 1px solid green; */
  font-size: 14px;
}
.song-list-card{
  display: flex;
  flex-direction: column-reverse;
  justify-content: center;
  align-items: center;
  position: absolute;
  padding: 5px 0;
  border: 1px solid var(--site-red);
  /* box-shadow: 3px 3px 3px var(--site-red); */
  color: black;
  background-color: aliceblue;
  z-index: 2;
  width: 50%;
  background-color: white;
}
.song-list-card .span-1,.song-list-card .span-2,
.song-list-card .span-3,.song-list-card .span-4,
.song-list-card .span-5,.song-list-card .span-6{
  display: flex;
  justify-content: flex-start;
  align-items: center;
  width: 100%;
  color: black;
  padding: 5px;
  border-bottom: 1px solid rgb(234, 231, 231);
}
.song-list-card .span-6{
  border-bottom: 0;
}
.song-li .song-li-wrap{
  display: flex;
  justify-content: space-between;
  align-items: center;
  position: relative;
  padding: 20px 5px;
  width: 100%;
  /* margin-right: 40px; */
  /* border: 1px solid green; */
  font-size: 14px;
  /* background-color: aqua; */
}
#site-black-color{
  background-color: var(--site-black);
}
#site-black-color:hover{
  border: 1px solid var(--site-green);
}
#normal-black-color{
  background-color: var(--normalblack);
}
#normal-black-color:hover{
  border: 1px solid var(--site-green);
}
#site-black-color.song-li-change-bg,
#normal-black-color.song-li-change-bg{
  background-color: var(--site-red);
}
/* .song-li:hover{
  border: 1px solid var(--site-green);
} */
.song-li .double-bar{
  font-size: 17px;
  margin: 0 5px;
}
.song-li .item{
  width: 5%;
  /* background-color: green; */
}
.song-li .item-1{
  display: flex;
  justify-content: flex-start;
  align-items: center;
  width: 30%;
  margin-right: 10px;
  flex-grow: 1;
  /* background-color: green; */
}
.song-li .item-1.re-spacing{
  padding-right: 5%;
  /* background-color: green; */
}
/* .song-li .item-1 .div:first-child.hide-place-1{
  display: none;
} */
.song-li .item-1 .place-1{
  display: flex;
  justify-content: space-between;
  align-items: center;
  /* background-color: red; */
}
/* .song-li .item-1 div:first-child.hide-div1{
  display: none;
} */
.song-li .item-1 div:nth-child(2).hide-div1{
  display: none;
}
.song-li .item-1 div:last-child.hide-div2{
  display: none;
}
.song-li .item-1 .place-2{
  margin-left: 20%;
  display: none;
  /* background-color: blue; */
}
.song-li .item-1 .place-2 .html-icon:hover{
  color:aqua;
}
.song-li .item-1 .place-2.display-place-2{
  display: flex;
  justify-content: space-between;
}
.song-li .item-1 .place-2 span:nth-child(1){
  /* position: absolute; */
  top: 0;
  left: 0;
  align-self: flex-end;
  margin-right: 20px;
  /* background-color: green; */
}
.song-li .item-1 .place-2 span:nth-child(2){
  /* position: absolute; */
  top: 0;
  right: -50px;
  /* background-color: green; */
}
.song-li .item-2{
  width: 30%;
  flex-grow: 1;
  /* background-color: green; */
}
.song-li .item-3,
.song-li .item-4{
  flex-grow: 1;
  /* background-color: green; */
}
  
@media screen and (max-width:768px){
    template.album-photos .body{
    }
    /* .song-li .song-li-wrap{
      width: 88%;
    } */
  } 
  @media screen and (max-width:760px){
      .song-li .song-li-wrap .item-3{
        display: none;
      }
      .song-li .song-li-wrap .item-3,
      .song-li .song-li-wrap .item-4{
        flex-grow: 0;
      }
    }
  @media screen and (max-width:500px){
      .song-li .song-li-wrap .item-4{
        display: none;
      }
    }