template.album-photos{
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: flex-start;
    width: 100%;
    height: 100%;
    overflow: hidden;
    /* background-color: blue; */
    margin-left: 20px ;
  }
  template.album-photos.no-display-album-photos{
    display: none;
  }
  template.album-photos .head{
    display: block;
    justify-content: center;
    align-items: center;
    width: 100%;
    /* padding-right: 40px; */
    padding: 0 55px 20px 0;
    /* background-color: red; */
  }
  template.album-photos .head h1{
    display: block;
    justify-content: center;
    align-items: center;
    margin: 0;
    padding: 0;
    padding-bottom: 2px;
  }
  template.album-photos .head .place-1{
    display: flex;
    justify-content: space-between;
    align-items: center;
    /* margin-right: 55px; */
    flex-wrap: wrap;
    /* background-color: blue; */
    /* width: 100%; */
  }
  template.album-photos .head .place-1 .box{
    display: flex;
    justify-content: space-between;
    align-items: center;
    margin-right: 5%;
    /* width: 50%; */
    /* background-color: red; */
  }
  template.album-photos .head .place-1 .box span{
    font-size: 13px;
    margin-right: 20px;
  }
  template.album-photos .head .place-1 .icon-place{
    display: flex;
    justify-content: center;
    align-items: center;
    margin-top: 10px;
  }
  template.album-photos .head .place-1 .icon-place #select,
  template.album-photos .head .place-1 .icon-place #cancel{
    display: flex;
    justify-content: center;
    align-items: center;
    /* background-color: green; */
    position: relative;
    display: flex;
  }
  template.album-photos .head .place-1 .icon-place #select .html-icon,
  template.album-photos .head .place-1 .icon-place #cancel .html-icon{
    background-color: grey;
    padding: 0 2px;
    font-size: 16px;
    font-weight:bold;
    margin-right: 5px;
  }
  template.album-photos .head .place-1 .icon-place #select.dont-display,
  template.album-photos .head .place-1 .icon-place #cancel.dont-display{
    display: none;
  }
  template.album-photos .head .place-1 .icon-place .icon{
    margin: 0 12px;
  }
  template.album-photos .head .place-1 .icon-place .filler{
    position: absolute;
    top:-5px;
    left:0;
    width: 110%;
    height: 110%;
  }
  template.album-photos .body{
    display: flex;
    flex-wrap: wrap;
    justify-content: space-between;
    align-items: flex-start;
    /* align-items: center; */
    overflow-y: auto;
    /* height: 58vh; */
    height: 100%;
    width: 100%;
    /* padding: 0 40px 40px 0; */
    /* background-color: yellow; */
  }
  template.album-photos .body-2{
    display: flex;
    flex-wrap: wrap;
    justify-content: space-between;
    align-items: flex-start;
    /* align-items: center; */
    overflow-y: auto;
    /* height: 58vh; */
    height: 100%;
    width: 100%;
    padding: 0 40px 40px 0;
    /* background-color: yellow; */
  }
  template.album-photos .in-body{
    display: flex;
    flex-wrap: wrap;
    justify-content: space-between;
    align-items: flex-start;
    /* width: 100%; */
    /* height: 100%; */
    background-color: green;
  }
  template.album-photos .sorted-item-wrap{
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: flex-start;
    width: 100%;
    margin: 0 0 30px 0;
  }
  template.album-photos .sorted-item-wrap .sort-head{
    /* background-color: rgb(176, 53, 53); */
    display: flex;
    justify-content: flex-start;
    align-items: flex-start;
    width: 100%;
    color: var(--normalwhite);
    font-size:20px;
    padding: 10px;
    margin-bottom: 20px ;
    background-color: var(--site-black);
    /* border-top:2px solid var(--site-green); */
    border-bottom:2px solid var(--site-green);
    border-radius: 5px 5px 0 0;
  }
  template.album-photos .sorted-item-wrap .sorted{
    display: flex;
    flex-wrap: wrap;
    justify-content: space-between;
    align-items: center;
    width: 100%;
    /* background-color: red; */
  }
  template.album-photos .sorted-item-wrap .sorted-2{
    display: flex;
    flex-wrap: wrap;
    justify-content: flex-start;
    align-items: center;
    padding-bottom: 10px;
    width: 100%;
    /* background-color: white; */
    border-bottom:2px solid var(--site-green);
    /* background-color: red; */
    /* overflow-y: auto; */
  }
  template.album-photos .sorted-item-wrap .sorted:last-child,
  template.album-photos .sorted-item-wrap .sorted-2:last-child{
    border-bottom:none;
  }
@media screen and (max-width:768px){
    template.album-photos .body{
      /* padding: 36px 20px 130px 15px; */
    }
    template.album-photos .head{
      padding: 0 40px 20px 0;
    }
  }