#header {
    width: 100%;
    height: 20vh;
    display: flex;
    justify-content: center;
    align-items: center;
    position: fixed;
    overflow: hidden;
    z-index:3;
    top:0;
    left:0;
    background-color: var(--site-black); 
    border:2px solid black;
    /* background-color: yellow; */
}
#header .content{
    display: flex;
    justify-content: space-between;
    align-items: center;
    width: 100%;
    /* background-color: blueviolet; */
    padding: 55px;
}
#header .content .box-1{
    display: flex;
    justify-content: space-between;
    align-items: center;
    width: 60%;
    background-color: rgb(176, 53, 53);
    padding: 5px;
    border-radius: 10px;
    margin-right: 5px;
}
#header .content .box-1 .site-icon{
    font-size: 20px;
    font-weight: bold;
    position: relative;
    margin-right: 50px;
}
#header .content .box-1 .site-icon span:first-child{
    font-size: 20px;
    height:100px;
    padding: 5px;
    background-color: green;
    border-radius: 100px;
    padding-right: 20px;
}
#header .content .box-1 .site-icon span:last-child{
    font-size: 20px;
    padding: 5px;
    color:green;
    padding-left: 20px;
    position: absolute;
    top:-5px;
    right: -65px;
    /* border:3px solid white; */
    background-color: white;
    border-radius: 100px;
}
#header .content .box-2{
    display: flex;
    justify-content: space-between;
    align-items: center;
    width: 30%;;
    /* background-color: rgb(208, 23, 23); */
    /* background-color: rgb(19, 163, 19); */
    border-radius: 10px;
    padding: 5px;
}
#header .content .box-2 .list-place{
    display: flex;
    justify-content: space-between;
    align-items: center;
    width: 100%;
}
#header .content .box-2 ul{
    display: flex;
    justify-content: space-between;
    align-items: center;
}
#header .content .box-2 ul li{
    display: flex;
    justify-content: space-between;
    align-items: center;
    padding: 10px 5%;
    font-weight: bold;
    font-size: 14px;
    margin: 0; 
    color:text-color;
    position: relative;
}
#header .content .box-2 ul li:hover{
    color: red;
}
#header .content .box-2 ul li:first-child{
    padding-left: 0;
}
#XMLID_1718_{
    color: red;
}
#header .content .box-2 .sign-up{
    margin-left: 50px;
    color:text-color;
    font-weight: bold;
}
#header .content .box-2 .menubar{
    position: relative;
    font-size: 50px;
    font-weight: bold;
    display: none;
}
#header .content .box-2 .menutimes{
    font-size: 45px;
    font-weight: bold;
    position: relative;
    display: none;
}
#header .content .box-2 .menutimes-filler{
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
}




@media screen and (max-width:900px){
    #header .content .box-1{
        width: auto;
    }
  }
@media screen and (max-width:769px){
    #header .content .box-2 .menubar{
        display: none;
    }
}
@media screen and (max-width:768px){
    #header .content{
        flex-wrap: wrap;
     }
     #header .content .box-2 .menubar{
         display: block;
     }
     #header .content .box-2 .list-place{
         display: none;
     }
     #header .content .box-2{
         width: auto;
         padding: 0;
         background-color: transparent;
     }
     #header .content .box-1{
        padding: 20px;
         /* background-color: transparent; */
     }
     #header .content{
         padding:40px;
     }
  }

