#now-playing {
  display: none;
  justify-content: center;
  align-items: flex-start;
  /* background-color: red; */
  width: 95%;
  /* padding: 20px; */
  height: 100%;
  overflow-y: auto;
}
#now-playing.display-now-playing{
  display: flex;
}
#now-playing .now-playing-wrap {
  width: 100%;
}

#now-playing .now-playing-wrap .img-holder,
#now-playing .now-playing-wrap .img-holder2{
  /* width: 190px;
  height: 220px; */
  /* background-color: green; */
  display: flex;
  align-items: center;
  justify-content: space-between;
  width: 100%;
  flex-grow: 1;
  overflow: hidden;
  margin-right: 10px;
  position: relative;
  background-color: red;
}
#now-playing .now-playing-wrap .img-holder2{
  justify-content: center;
}
#now-playing .now-playing-wrap .img-holder img,
#now-playing .now-playing-wrap .img-holder2 img{
  object-fit: cover;
  width: 100%;
  height: 220px;
}
#now-playing .now-playing-wrap .img-holder .img-txt{
  display: flex;
  flex-direction: column;
  align-items: flex-start;
  position: absolute;
  bottom: 30px;
  left: 30px;
  width: 300px;
}
#now-playing .now-playing-wrap .img-holder2 .img-txt{
  display: flex;
  flex-direction: column;
  align-items: flex-start;
  position: absolute;
  bottom: 50%;
  left: 20%;
  /* width: 300px; */
  /* background-color: red; */
}
#now-playing .now-playing-wrap .img-txt span{
  font-size: 20px;
  margin: 2px;
}
#now-playing .now-playing-wrap .img-holder .img-txt span:last-child{
  font-size: 30px;
  color: rgb(215, 204, 204);
}
#now-playing .now-playing-wrap .img-holder2 .img-txt span:last-child{
  font-size: 40px;
  color: rgb(215, 204, 204);
}
#now-playing .now-playing-wrap .img-holder .img-icon{
  display: flex;
  justify-content: center;
  align-items: center;
  position: absolute;
  bottom: 30px;
  right: 30px;
  font-size: 30px;
}
#now-playing .now-playing-wrap .img-holder .img-icon span{
  display: flex;
  justify-content: center;
  align-items: center;
  padding: 5px;
  border-radius: 50px;
  height: 40px;
  width: 40px;
  border: 2px solid rgb(215, 204, 204);
  color:rgb(215, 204, 204);
}
#now-playing .now-playing-wrap .img-holder .img-icon span:hover{
  background-color: var(--lightgrey);
}
#now-playing .now-playing-wrap .img-holder .img-icon span:first-child{
  margin-right: 20px;
}



#now-playing .song-li{
  display: flex;
  justify-content: flex-start;
  align-items: center;
  position: relative ;
  width: 100%;
  /* border: 1px solid green; */
  margin-top: 50px;
  font-size: 14px;
}
#now-playing .song-list-card{
  display: flex;
  flex-direction: column-reverse;
  justify-content: center;
  align-items: center;
  position: absolute;
  padding: 5px 0;
  border: 1px solid var(--site-red);
  /* box-shadow: 3px 3px 3px var(--site-red); */
  color: black;
  background-color: aliceblue;
  z-index: 2;
  width: 50%;
  background-color: white;
}
#now-playing .song-list-card .span-1, #now-playing .song-list-card .span-2,
#now-playing .song-list-card .span-3, #now-playing .song-list-card .span-4,
#now-playing .song-list-card .span-5, #now-playing .song-list-card .span-6{
  display: flex;
  justify-content: flex-start;
  align-items: center;
  width: 100%;
  color: black;
  padding: 5px;
  border-bottom: 1px solid rgb(234, 231, 231);
}
#now-playing .song-list-card .span-6{
  border-bottom: 0;
}
#now-playing .song-li .song-li-wrap{
  display: flex;
  justify-content: space-between;
  align-items: center;
  position: relative;
  padding: 20px 5px;
  width: 100%;
  background-color: var(--site-black-2);
  /* border: 1px solid green; */
  font-size: 14px;
  /* background-color: aqua; */
}
/*
#now-playing #site-black-color.song-li-change-bg,
#now-playing #normal-black-color.song-li-change-bg{
  background-color: var(--site-red);
} 
*/

#now-playing .song-li .double-bar{
  font-size: 17px;
  margin: 0 5px;
}
#now-playing .song-li .item{
  width: 5%;
  /* background-color: green; */
}
#now-playing .song-li .item-1{
  display: flex;
  justify-content: flex-start;
  align-items: center;
  width: 30%;
  margin-right: 10px;
  flex-grow: 1;
  /* background-color: green; */
}
#now-playing .song-li .item-1.re-spacing{
  padding-right: 5%;
  /* background-color: green; */
}
/* .song-li .item-1 .div:first-child.hide-place-1{
  display: none;
} */
#now-playing .song-li .item-1 .place-1{
  display: flex;
  justify-content: space-between;
  align-items: center;
  /* background-color: red; */
}
/* .song-li .item-1 div:first-child.hide-div1{
  display: none;
} */
#now-playing .song-li .item-1 div:nth-child(2).hide-div1{
  display: none;
}
#now-playing .song-li .item-1 div:last-child.hide-div2{
  display: none;
}
#now-playing .song-li .item-1 .place-2{
  margin-left: 20%;
  display: none;
  /* background-color: blue; */
}
#now-playing .song-li .item-1 .place-2 .html-icon:hover{
  color:aqua;
}
#now-playing .song-li .item-1 .place-2.display-place-2{
  display: flex;
  justify-content: space-between;
}
#now-playing .song-li .item-1 .place-2 span:nth-child(1){
  /* position: absolute; */
  top: 0;
  left: 0;
  align-self: flex-end;
  margin-right: 20px;
  /* background-color: green; */
}
#now-playing .song-li .item-1 .place-2 span:nth-child(2){
  /* position: absolute; */
  top: 0;
  right: -50px;
  /* background-color: green; */
}
#now-playing .song-li .item-2{
  width: 30%;
  flex-grow: 1;
  /* background-color: green; */
}
#now-playing .song-li .item-3,
#now-playing .song-li .item-4{
  flex-grow: 1;
  /* background-color: green; */
}
  
@media screen and (max-width:768px){
    section.album-photos .body{
    }
  } 
  @media screen and (max-width:760px){
      .song-li .song-li-wrap .item-3{
        display: none;
      }
      .song-li .song-li-wrap .item-3,
      .song-li .song-li-wrap .item-4{
        flex-grow: 0;
      }
    }
  @media screen and (max-width:500px){
      .song-li .song-li-wrap .item-4{
        display: none;
      }
    }