  .items{
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: flex-start;
    position: relative;
    width: 145px;
    /* height: 150px; */
    padding: 3px;
    margin: 0 40px 30px 0;
  }
  .items.pointer{
    cursor: pointer;
  }
  .items:hover{
    background-color: var(--site-black); 
  }
  .items.is-checked{
    background-color: #1b1b81;
  }
  .items.uncheck{
    background-color: transparent;
  }
  .items .text-wrap{
    margin-top: 5px;
    /* background-color: blue; */
  }
  .items .text-wrap .text{
    display: flex;
    justify-content: flex-start;
    align-items: center;
    width: 100%;
  }
  .items .text-wrap span:first-child{
    margin-right: 5px;
    font-size: 13px;
  }
  .items .text-wrap span:last-child{
    margin-right: 5px;
    font-size: 11px;
    color: green;
  }
  .items .img-holder{
    width: 100%;
    position: relative;
  }
  /* template.album-photos .body .img-holder .filler{
    position: absolute;
    display: none;
    top:0;
    left:0;
    background-color: red;
    width: 100%;
    height: 100%;
  } */
  .items .img-holder .filler.display-filler{
    display: block;
  }
  .items .img-holder img{
    object-fit: cover;
    width: 100%;
    height: 150px;
    /* background-color: green; */
  }
  
@media screen and (max-width:550px){
  .items{
      flex-grow: 1;
    }
  }