#artist-view{
  display: none;
  flex-wrap: wrap;
  justify-content: center;
  align-items: center;
  /* background-color: yellow; */
  width: 100%;
  overflow-y: auto;
  height: 100%;
  padding-bottom: 50px;
}
#artist-view.adjust-for-btm-pallete{
  height:65vh;
  animation: reduceHeight 500ms ease-in 1;
}
#artist-view.display-artist-view{
  display: flex;
}
#artist-view .artist-view-body{
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  width: 95%;
  /* background-color: aqua; */
}
#artist-view h2{
  padding: 0;
  margin: 0;
  margin-bottom: 30px;
  font-size: 20px;
}
#artist-view .block-1{
  display: flex;
  /* flex-wrap: wrap; */
  justify-content: space-between;
  align-items: flex-start;
  width: 100%;
  margin-bottom: 50px;
  /* border: 1px solid white; */
}
#artist-view .block-1 .img-holder{
  /* width: 190px;
  height: 220px; */
  /* background-color: green; */
  flex-grow: 1;
  overflow: hidden;
  margin-right: 10px;
}
#artist-view .block-1 .img-holder img{
  object-fit: cover;
  width: 190px;
  height: 220px;
  flex-grow: 1;
  border-radius: 10px;
}
#artist-view .block-1 .box-2{
  display: flex;
  flex-wrap: wrap;
  flex-grow: 1;
  justify-content: space-between;
  align-items: center;
  width: 75%;
  /* background-color: grey; */
}
#artist-view .block-1 .box-2 .place-1{
  display: flex;
  flex-wrap: wrap;
  flex-direction: column;
  justify-content: center;
  align-items: flex-start;
  /* background-color: yellow; */
  /* width: 75%; */
  /* border: 1px solid white; */
}
#artist-view .block-1 .box-2 .place-1 .text-1{
  font-size: 40px;
  font-weight: bold;
  padding: 0 0 20px 0;
}
#artist-view .block-1 .box-2 .place-1 .text-2{
  font-size: 14px;
  color: var(--darkgrey);
  margin-bottom: 5px;
}
#artist-view .block-1 .box-2 .place-1 .text-3{
  font-size: 14px;
  padding: 0 0 20px 0;
  color: var(--darkgrey);
}
#artist-view .block-1 .box-2 .place-1 .btns{
  display: flex;
  flex-wrap: wrap;
  justify-content: space-between;
  align-items: center;
  /* background-color: red; */
  width: 100%;
}
#artist-view .block-1 .box-2 .place-1 .btns .html-icon{
  border-radius: 20px;
}
#artist-view .block-1 .box-2 .place-1 .btns .btn-txt{
  margin-left: 15px;
}
#artist-view .block-1 .box-2 .place-2{
  display: flex;
  justify-content: flex-end;
  align-items: center;
  align-self: flex-end;
  /* border: 1px solid white; */
}



#artist-view .block-2{
  display: flex;
  justify-content: space-between;
  align-items: flex-start;
  width: 100%;
  margin-bottom: 50px;
  /* border: 1px solid white; */
  /* background-color:blue;
  padding: 10px; */
}
#artist-view .block-2 .box-1{
  display: flex;
  flex-direction: column;
  justify-content: flex-start;
  align-items: flex-start;
  flex-grow: 1;
  margin-right: 10px;
  /* background-color: green; */
}
#artist-view .block-2 .box-2{
  display: flex;
  flex-wrap: wrap;
  justify-content: space-between;
  align-items: center;
  /* background-color: grey; */
  width: 75%;
  flex-grow: 1;
}
#artist-view .block-2 .box-1 .place-1,
#artist-view .block-2 .box-1 .place-2{
  display: flex;
  justify-content: space-between;
  align-items: center;
  margin-bottom: 20px;
}
#artist-view .block-2 .box-1 .place-1 img,
#artist-view .block-2 .box-1 .place-2 img{
  width: 80px;
  height: 80px;
  border-radius: 80px;
  object-fit: cover;
  margin-right: 20px;
}
#artist-view .block-2 .box-2{
  display: flex;
  justify-content: space-between;
  align-items: flex-start;
  /* border: 1px solid white; */
  width: 75%;
  /* background-color: blue; */
  /* height: 100px; */
}
#artist-view .block-2 .box-2 .place-1{
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: flex-start;
  width: 100%;
  /* margin-bottom: 30px; */
}
#artist-view .block-2 .box-2 .place-1 .place-1-box{
  display: flex;
  justify-content: space-between;
  align-items: center;
  margin-bottom: 10px;
  width: 100%;
}
#artist-view .block-2 .box-2 .place-1 .place-1-box .place1{
  display: flex;
  justify-content: center;
  align-items: center;
  margin-bottom: 20px;
}
#artist-view .block-2 .box-2 .place-1 .place-1-box img{
  width: 55px;
  height: 60px;
  border-radius: 10px;
  object-fit: cover;
  margin-right: 13px;
}
#artist-view .block-2 .box-2 .place-1 .place-1-box .txt{
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: flex-start;
  margin-bottom: 10px;
}
#artist-view .block-2 .box-2 .place-1 .place-1-box .txt span:first-child{
  font-size: 18px;
}
#artist-view .block-2 .box-2 .place-1 .place-1-box .txt span:last-child{
  font-size: 12px;
  color: var(--darkgrey);
}




#artist-view .block-3{
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: flex-start;
  width: 100%;
  padding: 20px 0;
  overflow: hidden;
  margin-bottom: 20px;
}
#artist-view .block-3 h2{
  font-size: 25px;
  margin: 0;
}
#artist-view .block-3 .place-1{
  display: flex;
  justify-content: space-between;
  align-items: flex-start;
  width: 100%;
  padding: 20px 0;
  overflow: hidden;
  margin-bottom: 20px;
}
#artist-view .block-3 .place-1:hover{
  overflow-x: auto;
}
#artist-view .block-3 .box{
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  width: 100%;
  /* border: 1px solid white; */
  margin-right: 20px;
}
#artist-view .block-3 .box {
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  width: 100%;
  /* border: 1px solid white; */
  margin-right: 20px;
}
#artist-view .block-3 .box img{
  object-fit: cover;
  width: 150px;
  height: 130px; 
  border-radius: 10px;
}
#artist-view .block-3 .box .txt{
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: flex-start;
  margin: 10px 0;
}
#artist-view .block-3 .box .txt span:first-child{
  font-size: 18px;
  margin-bottom: 5px;
}
#artist-view .block-3 .box .txt span:last-child{
  font-size: 12px;
  color: var(--darkgrey);
}





#artist-view .block-4{
  display: flex;
  justify-content: space-between;
  align-items: flex-start;
  width: 100%;
  padding: 20px;
  border: 1px solid var(--darkgrey);
  overflow: hidden;
  padding: 30px;
  border-radius: 10px;
}
.thead, .thead h1{
  padding: 0;
  margin: 0;
}
.thead{
  margin-bottom: 10px;
}
.table{
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: flex-start;
  width: 100%;
  padding: 10px 5%;
}
.table .tbody{
  display: flex;
  flex-direction: column;
  justify-content: space-between;
  align-items: flex-start;
  width: 100%;
  border: 1px solid var(--darkgrey);
  overflow: hidden;
}
.table .tbody .cols{
  display: flex;
  justify-content: center;
  align-items: center;
  width: 100%;
  border-bottom: 0.1px solid var(--darkgrey);
  /* background-color: aquamarine; */
}
.table .tbody .cols span{
  font-size: 15px;
}
.table .tbody .cols span:first-child{
  width: 30%;
  border-right: 0.1px solid var(--darkgrey);
  padding: 10px;
  color: rgb(218, 210, 210);
}
.table .tbody .cols span:last-child{
  width: 70%;
  /* border: 0.1px solid var(--darkgrey); */
  padding: 10px;
  color: rgb(218, 210, 210);
}






@media screen and (max-width:768px){
}
@media screen and (max-width:760px){
  #artist-view .block-1,
  #artist-view .block-2{
    flex-wrap: wrap;
  }
  #artist-view .block-1 .box-1{
    margin: 0;
  }
  #artist-view .block-1 .box-2 .place-1{
    /* flex-grow: 1; */
  }
  #artist-view .block-1 .box-2 .place-1 .text-1{
    font-size: 30px;
  }
  #artist-view .block-1 .box-2,
  #artist-view .block-2 .box-2{
    margin-top: 50px;
    /* background-color: green; */
  }
}

